@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{
	margin: 0;
	font-family: 'Roboto', sans-serif;
}
h1{font-size:2rem}p{font-size:1.1rem}nav{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:100%;-webkit-justify-content:flex-end;justify-content:flex-end}nav ul{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}nav ul li{list-style:none;padding:0 .5rem;cursor:pointer}nav .nav{width:60%;display:-webkit-flex;display:flex;padding:0 10rem}nav .nav li{padding:0 2rem}nav .btns{-webkit-flex:1 1;flex:1 1;-webkit-justify-content:center;justify-content:center;padding:0 10rem}nav .btns button{background-color:rgba(0,0,0,0);border:0;padding:0 2rem;border-radius:50px;cursor:pointer}nav .btns .signUpBtn{background-color:#2acfcf;color:#fff}nav .btns .signUpBtn:hover{opacity:60%}

.container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%}.container .content{background-image:url(/shortly-url/static/media/bg-shorten-desktop.0d9742f4.svg);background-repeat:no-repeat;background-position:center;background-size:cover;background-color:#232127;padding:3rem;border-radius:10px}.container .content form{width:100%}.container .content input{border:0;width:35vw;height:3rem;margin:0 1rem;border-radius:10px;padding:0 0.5em;font-size:1em}.container .content button{background-color:#2acfcf;color:#fff;border:0;padding:0 2em;height:3rem;border-radius:10px;cursor:pointer}.container .content button:hover{opacity:60%}.container .content .show{padding:2rem 0 0 0;color:#bfbfbf}.container .content .copy{background-color:rgba(255,255,255,0);margin:0 1rem;font-size:.7rem;border:1px dashed #bfbfbf;padding:.2rem;height:unset;border-radius:0}

